.text-1row {
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap; }

.text-2row {
  display: -webkit-box;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
  overflow: hidden;
  text-overflow: ellipsis; }

.text-select {
  -webkit-user-select: text;
  user-select: text; }

.word-break {
  word-wrap: break-word;
  word-break: break-all; }

.word-normal {
  word-wrap: normal; }

.hyphens {
  word-wrap: break-word;
  -webkit-hyphens: auto;
  hyphens: auto; }

/*---------------------------------
vertical
---------------------------------*/
.vtc-t {
  vertical-align: top; }

.vtc-m {
  vertical-align: middle; }

.vtc-b {
  vertical-align: bottom; }

.self-s {
  align-self: flex-start; }

.self-c {
  align-self: center; }

.self-e {
  align-self: flex-end; }

/*---------------------------------
layout
---------------------------------*/
.fix-t, .fix-b, .float-t, .float-b {
  position: -webkit-sticky !important;
  position: fixed !important;
  left: 0 !important;
  z-index: 99 !important; }

.fix-t {
  top: 0 !important; }

.fix-b {
  bottom: 0 !important; }

.float-t {
  top: 0; }

.float-b {
  bottom: 0; }

.hide {
  display: none !important; }

.show {
  display: block !important; }

.visible {
  visibility: visible !important; }

.hidden {
  visibility: hidden !important; }

.sr-only {
  position: absolute;
  width: 1px;
  height: 1px;
  padding: 0;
  margin: -1px;
  overflow: hidden;
  clip: rect(0, 0, 0, 0);
  border: 0; }

.pull-left {
  float: left !important; }

.pull-right {
  float: right !important; }

.clearfix, .cl {
  zoom: 1; }
  .clearfix:after, .cl:after {
    content: "";
    display: block;
    height: 0;
    clear: both;
    visibility: hidden; }

